.dark-pink {
  color: #D07991
}

.light-pink {
  color: #F5D4DB
}

.white {
  color: white;
}

.app {
  text-align: center;
  color: white;
}

ul li {
  display: inline;
  list-style-type: none;
}

ul.app-menu {
  margin: 0;
  padding-top: 20px;
}

ul.app-menu li {
  margin: 0 60px;
  font-size: 40px;
  font-weight: bold;
  color: #D07991;
}

#app-nav {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 10px;
  background-color: rgba(210, 210, 210, .8);
  z-index: 10;
  -webkit-transition: top 0.3s ease-out;
  -o-transition: top 0.3s ease-out;
  transition: top 0.3s ease-out;
}

#app-nav a{
  margin: 0 50px;
  color: #D07991;
}

#app-nav a.nav-active {
  color: white;
}

#app-nav.hidden {
  top: -100px;
}

.contacts {
  background-color: white;
  color: #F5D4DB;
  margin: 0;
  padding: 30px 40px;
}

ul.contacts li { 
  /* background-color: #F5D4DB;
  color: white;
  padding: 40px 30px; */
  margin: 0 5%;
  border-radius: 50%;
}

ul.contacts a {
  color: #D07991;
}

ul.contacts a:hover {
  color: #F5D4DB;
}


